@font-face {
  font-family: 'icomoon';
  src:  url(../../static/media/icomoon.b2cb1869.eot);
  src:  url(../../static/media/icomoon.b2cb1869.eot#iefix) format('embedded-opentype'),
    url(../../static/media/icomoon.8d00fdd3.ttf) format('truetype'),
    url(../../static/media/icomoon.3675919e.woff) format('woff'),
    url(../../static/media/icomoon.8f8e11fb.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-facebook:before {
  content: "\EA90";
}
.icon-twitter:before {
  content: "\EA96";
}

